import { Component } from '@angular/core';
import { HostListener} from "@angular/core";
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  api_users = "https://api.github.com/users?since=";
  github_logo = "https://s-media-cache-ak0.pinimg.com/originals/3c/d5/67/3cd5679f54dc60811383649f9f6ea37d.png";
  angular_logo = "https://angular.io/resources/images/logos/angular/angular_solidBlack.png";
  users = [];

  constructor(private http:Http){
    this.getUsers(0);
  }

  getUsers(start){
    this.http.get(this.api_users+start)
             .map(res => res.json())
             .subscribe(users => this.users = this.users.concat(users));
  }

  @HostListener("window:scroll", [])
  onWindowScroll() {
    var scrollTop = (document.documentElement && document.documentElement.scrollTop) || document.body.scrollTop;
    var scrollHeight = (document.documentElement && document.documentElement.scrollHeight) || document.body.scrollHeight;
    var scrolledToBottom = (scrollTop + window.innerHeight) >= scrollHeight;
    //console.log(scrolledToBottom);
    if(scrolledToBottom)
      this.getUsers(this.users.length);
  }
}



